body {
  overflow-x: hidden;
  text-decoration: none;
}
nav {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  background: #87cbff;
  padding: 10px 10px;
  gap: 50%;
}

.griid {
  display: grid;
  grid-template-columns: auto auto auto;
}

@media screen and (max-width: 800px) {
  .griid {
    display: grid;
    grid-template-columns: auto auto;
  }

  .n {
    display: block;
    margin: 0px;
    /* justify-content: center */
  }
  .flexx {
    display: block;
  }
}
@media screen and (max-width: 400px) {
  .griid {
    display: grid;
    grid-template-columns: auto;
  }
  .n {
    display: block;
    margin: 0px;
    /* justify-content: center */
  }
  .flexx {
    display: block;
  }
}

.flexx {
  display: flex;
  justify-content: center;
}

nav .tabs {
  padding-right: 20px;
}

nav a {
  text-decoration: none;
  font-size: 30px;
  padding-left: 5px;
  color: #252a34;
}
a {
  text-decoration: none;
  color: #252a34;
}

.Card img {
  width: 23rem;
  height: 167px;
  object-fit: cover;
  transition: all 200ms ease-in;
}
.Card img:hover {
  width: 23rem;
  height: 167px;
  object-fit: cover;
  transform: scale(1.1);
}

.Card {
  width: 20rem;
  display: flex;
  flex-direction: column;
  margin: 20px;
  background: rgba(0, 0, 0, 0.054);
  padding-bottom: 40px;
  overflow: hidden;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-right: 20px;
  padding-left: 20px;
}

.btn {
  padding: 1rem 2rem;
  background: #87cbff;
  border: none;
  border-radius: 5px;
  box-shadow: #252a3424 2px 3px 0px;
  transition: 200ms ease-in all;
  cursor: pointer;
}
.btn:hover {
  padding: 1rem 2rem;
  background: #87cbff;
  border: none;
  border-radius: 5px;
  box-shadow: #252a3424 20px 23px 0px;
}

.n {
  margin: 1.2rem;
}

.m {
  float: right;
}
